<template>
  <div class="wrap-g live">
    <header>
      <img src="../../../static/img/home/logo.png" style="width: 30px;height: 30px;float:left;;padding: 5px;box-shadow: 5px 0px 5px -5px rgba(0,0,0,0.50);">
      <img src="../../../static/img/home/fenlei.png" style="width: 20px;height: 20px;padding: 10px;float: right;box-shadow: -5px 0px 5px -5px rgba(0,0,0,0.50);">

      <el-tabs v-model="activeName" @tab-click="handleClick" style="font-size: 12px">
        <el-tab-pane label="以往小视频" name="/smallvideo"></el-tab-pane>
        <el-tab-pane label="正在直播" name="/liveing"></el-tab-pane>
        <el-tab-pane label="直播倒计时" name="/livecountdown"></el-tab-pane>
        <el-tab-pane label="一对一直播" name="/onelive"></el-tab-pane>
        <el-tab-pane label="以往直播" name="/pastlive"></el-tab-pane>
      </el-tabs>
    </header>
    <div class="main" style="margin-top: 40px;">
      <transition name="fade" mode="in-out">
        <router-view ></router-view>
      </transition>
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        activeName:"/smallvideo",
      }
    },
    methods:{
      handleClick(e){
        this.$router.push({ path: e.name});
        console.log(e.name)
      },

    },
    mounted(){
      this.activeName=this.$route.path;
    },
  }
</script>


<style  scoped>


</style>



